@import 'iconmoon-variables';

@font-face {
    font-family: '#{$icomoon-font-family}';
    src: url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?otn9ez');
    src: url('#{$icomoon-font-path}/#{$icomoon-font-family}.eot?otn9ez#iefix')
            format('embedded-opentype'),
        url('#{$icomoon-font-path}/#{$icomoon-font-family}.ttf?otn9ez')
            format('truetype'),
        url('#{$icomoon-font-path}/#{$icomoon-font-family}.woff?otn9ez')
            format('woff'),
        url('#{$icomoon-font-path}/#{$icomoon-font-family}.svg?otn9ez##{$icomoon-font-family}')
            format('svg');
    font-weight: normal;
    font-style: normal;
    font-display: block;
}

.icon {
    /* use !important to prevent issues with browser extensions that change fonts */
    font-family: '#{$icomoon-font-family}' !important;
    speak: never;
    font-style: normal;
    font-weight: normal;
    font-variant: normal;
    text-transform: none;
    line-height: 1;

    /* Better Font Rendering =========== */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

.icon-phone {
    &:before {
        content: $icon-phone;
    }
}
.icon-location2 {
    &:before {
        content: $icon-location2;
    }
}
.icon-cross {
    &:before {
        content: $icon-cross;
    }
}
.icon-checkmark {
    &:before {
        content: $icon-checkmark;
    }
}
.icon-mail4 {
    &:before {
        content: $icon-mail4;
    }
}
.icon-facebook2 {
    &:before {
        content: $icon-facebook2;
    }
}
.icon-instagram {
    &:before {
        content: $icon-instagram;
    }
}
.icon-telegram {
    &:before {
        content: $icon-telegram;
    }
}
.icon-twitter {
    &:before {
        content: $icon-twitter;
    }
}
