.header {
    background: #f9f9f9;
    border-bottom: 1px solid $base-border-color;

    .container {
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__contacts {
        align-items: center;
        font-size: 1.3rem;
    }

    &__phones {
        margin-right: 2rem;

        .tel-link + .tel-link {
            margin-left: 1rem;
        }
    }

    @media (max-width: 991px) {
        &__phones {
            .tel-link {
                display: block;

                & + .tel-link {
                    margin-left: 0;
                    margin-top: 0.5rem;
                }
            }
        }
    }

    @media (max-width: 767px) {
        padding-bottom: 1rem;

        .container {
            flex-direction: column;
        }

        &__logo {
            border-bottom: 1px solid $base-border-color;
        }

        &__contacts {
            padding-top: 0.7rem;
            flex-direction: column;
        }

        &__social {
            margin-top: 0.7rem;
        }

        .tel-link {
            display: block;
            margin: 0;
            font-size: 1.4rem;

            & + .tel-link {
                margin: 0;
                margin-top: 0.7rem;
            }
        }
    }
}

.logo {
    display: flex;

    &__image {
        width: 56px;
    }

    &__text {
        font-weight: bold;
        padding: 0.8rem 0;
        margin-left: 0.5rem;
        font-size: 1.4rem;
        line-height: 1.2;
    }

    &__text-city {
        display: block;
        color: $logo-bg;
    }

    @media (max-width: 991px) {
        .logo {
            &__text {
                padding: 1rem 0;
                font-size: 1.2rem;
            }
        }
    }
}

.nav {
    background: #595959;
    color: #fff;

    &__list {
        display: flex;
        margin-bottom: 0;
        justify-content: space-between;
        align-items: center;
        list-style: none;
    }

    &__item {
        display: flex;
        flex-shrink: 1;
        flex-grow: 1;
        justify-content: center;
        flex-basis: 0;
        align-items: center;
    }

    &__link {
        display: block;
        width: 100%;
        padding: 1rem 0.5rem;
        color: #fff;
        font-size: 1.2rem;
        line-height: 1;
        text-decoration: none;
        font-weight: 500;
        text-align: center;
        transition: all 0.3s;

        &:hover,
        &:focus {
            color: $active-color;
            background: #3e3d3b;
        }
    }

    @media (max-width: 767px) {
        &__list {
            padding: 0;
            flex-direction: column;
        }

        &__item {
            width: 100%;

            &:not(:last-child) {
                border-bottom: 1px solid $base-border-color;
            }
        }
    }
}

.screen-one {
    position: relative;
    padding: 8rem 0;
    background: url('../img/first-screen.jpg') center no-repeat;
    background-size: cover;

    &:before {
        content: '';
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        bottom: 0;
        left: 0;
        background: rgba(0, 0, 0, 0.4);
        z-index: 1;
    }

    .container {
        position: relative;
        z-index: 2;
        text-align: center;
    }

    .tel-link {
        display: block;
        margin-bottom: 1rem;
        font-size: 2rem;
        color: $secondary-color;

        &:last-child {
            margin-bottom: 0;
        }
    }

    h1 {
        margin-bottom: 0.5rem;
        color: $secondary-color;
    }

    h3 {
        margin-bottom: 3rem;
        color: #fff;
    }

    &__contacts {
        margin-bottom: 3rem;
    }

    @media (max-width: 767px) {
        padding: 2rem 0;
    }
}

.screen-two {
    padding-bottom: 0;
    background: #fff;
}

#services {
    padding-bottom: 3rem;
}

.service {
    justify-items: center;
    align-items: center;
    justify-content: center;

    &__img {
        text-align: center;

        img {
            width: 170px;
        }
    }

    p {
        margin-bottom: 1rem;
    }

    strong {
        display: block;
        margin-bottom: 1rem;
        color: $active-color;
        font-size: 1.4rem;
    }

    @media (max-width: 991px) {
        & + & {
            margin-top: 2rem;
        }
    }

    @media (max-width: 767px) {
        .btn {
            width: 100%;
        }

        img {
            margin-top: 2rem;
        }
    }
}

.gallery {
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;

    &__item {
        padding: 0.5rem;
        width: 25%;
    }

    &__link {
        display: block;
        width: 100%;
        overflow: hidden;
        border: 3px solid white;
        border-radius: 0.3rem;
        position: relative;
        padding-bottom: 60%;
        transition: border-top-color 0.1s, border-right-color 0.2s 0.1s,
            border-bottom-color 0.3s 0.2s, border-left-color 0.4s 0.3s;

        img {
            top: 0;
            left: 0;
            position: absolute;
            width: 100%;
            height: auto;
            transition: 0.5s transform;
            border-radius: 0.3rem;
        }

        &:hover,
        &:focus {
            border-color: $active-color;

            img {
                transform: scale(1.2);
            }
        }
    }

    @media (max-width: 991px) {
        &__item {
            width: #{calc(100% / 3)};
        }
    }

    @media (max-width: 600px) {
        &__item {
            width: 50%;
        }
    }
}

.screen-about {
    background: #fff;
    padding-bottom: 3rem;
}

.features {
    padding: 0;
    display: flex;
    margin-bottom: 3rem;
    justify-content: center;
    list-style: none;

    &__item {
        width: 33%;
        text-align: center;
        padding: 0 2rem;
    }

    @media (max-width: 767px) {
        flex-wrap: wrap;

        &__item {
            width: 100%;
        }
    }
}

.guarantee {
    background: #eee;

    &__description {
        padding: 1.5rem 3rem;

        h3 {
            font-size: 2.4rem;
            margin-bottom: 3rem;
        }

        p {
            font-size: 1.4rem;
            margin-bottom: 2rem;
        }
    }

    &__items {
        padding: 1.5rem 2rem;
        text-align: center;
    }

    &__item {
        img {
            width: 100px;
            height: auto;
        }

        h5 {
            font-weight: 500;
        }
    }

    @media (max-width: 767px) {
        &__description {
            padding: 1.5rem;
            padding-bottom: 0;

            p:last-child {
                margin-bottom: 0;
            }
        }

        &__items {
            padding: 1rem 1.5rem;
        }

        &__description {
            h3 {
                font-size: 2rem;
            }
        }
    }
}

.price-include {
    margin-top: 2rem;
    padding: 1.5rem 3rem;
    background: #eee;

    &__wrap {
        display: flex;
    }

    h3 {
        font-size: 2.4rem;
    }

    p {
        font-size: 1.6rem;
        margin-bottom: 2rem;
    }

    &__row {
        width: 50%;

        h4 {
            font-size: 2rem;
            margin-bottom: 1.5rem;
        }

        ul {
            margin-bottom: 0;
        }

        li {
            font-size: 1.4rem;
            margin-bottom: 1rem;

            &:last-child {
                margin-bottom: 0;
            }
        }
    }

    @media (max-width: 991px) {
        &__wrap {
            flex-wrap: wrap;
        }

        &__row {
            width: 100%;
        }
    }

    @media (max-width: 767px) {
        padding: 1rem 1.5rem;

        h3 {
            font-size: 2rem;
        }

        h4 {
            font-size: 1.7rem;
        }

        ul {
            padding-right: 1rem;
        }
    }
}

.steps {
    padding: 0;
    margin-top: 2rem;
    margin-bottom: 0;
    text-align: center;
    list-style: none;
    align-content: stretch;

    li {
        margin-top: 1rem;
        margin-bottom: 1rem;
    }

    &__item {
        height: 100%;
        border-radius: 5px;
        padding: 1rem;
        background: #fff;
        box-shadow: 0px 0px 10px #777;

        &:hover {
            figure {
                background: lighten($secondary-color, 40%);
            }
        }

        figure {
            display: flex;
            align-items: center;
            justify-content: center;
            width: 120px;
            height: 120px;
            margin: 0 auto 1rem;
            border-radius: 50%;
            border: 2px solid $secondary-color;
            transition: background-color 0.3s;

            svg {
                width: 70px;
                height: 70px;
            }
        }

        h5 {
            &:after {
                content: '';
                display: block;
                width: 60%;
                height: 2px;
                background: $secondary-color;
                margin: 1rem auto;
            }
        }
    }
}

.screen-reviews {
    padding-bottom: 3rem;

    .carousel {
        max-width: 600px;
        margin: 0 auto;

        &-item {
            text-align: center;
        }

        &-indicators {
        }
    }
}

.reviews {
    margin: 0 auto 1rem;
}

.review {
    padding-bottom: 2rem;
}

.calc {
    padding: 1.5rem;
    border: 1px solid #eee;
    background-color: #fff;
    border-radius: 5px;
    max-width: 500px;
    margin: 0 auto;
    box-shadow: 0px 0px 10px #777;

    &__result {
        font-size: 1.4rem;
        margin-bottom: 1rem;
    }

    &__result-sum {
        color: $active-color;
        font-weight: bold;
    }
}

.contacts {
    .icon {
        display: inline-block;
        margin-right: 0.3rem;
        color: $active-color;
    }

    &__phones {
        margin-bottom: 1rem;
        font-size: 1.4rem;
    }

    &__address {
        margin-bottom: 1rem;
        font-size: 1.4rem;
    }

    &__email {
        margin-bottom: 1rem;
        font-size: 1.4rem;
    }

    &__social {
        margin-bottom: 1rem;
    }

    &__visit {
        max-width: 100%;
        width: 360px;
    }
}

.footer {
    background: #fff;

    &__copyright {
        background: #595959;
        color: #fff;
        padding: 1rem;
        text-align: center;

        p {
            margin: 0;
        }
    }
}
