.review { padding-bottom: 2rem; }

.calc { padding: 1.5rem; border: 1px solid #eee; background-color: #fff; border-radius: 5px; max-width: 500px; margin: 0 auto; box-shadow: 0px 0px 10px #777; }

.calc__result { font-size: 1.4rem; margin-bottom: 1rem; }

.calc__result-sum { color: #fe6557; font-weight: bold; }

.contacts .icon { display: inline-block; margin-right: 0.3rem; color: #fe6557; }

.contacts__phones { margin-bottom: 1rem; font-size: 1.4rem; }

.contacts__address { margin-bottom: 1rem; font-size: 1.4rem; }

.contacts__email { margin-bottom: 1rem; font-size: 1.4rem; }

.contacts__social { margin-bottom: 1rem; }

.contacts__visit { max-width: 100%; width: 360px; }

.footer { background: #fff; }

.footer__copyright { background: #595959; color: #fff; padding: 1rem; text-align: center; }

.footer__copyright p { margin: 0; }
