@import url('https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap');

html {
    font-size: 14px;

    @media (max-width: 991px) {
        font-size: 14px;
    }
}

body {
    font-family: 'Roboto', sans-serif;
    background-color: #fdfdfd;
    background-image: url('../img/bg.jpg');
    background-position: center;
    font-size: 1.1rem;
    color: $base-color;
}

.container {
    min-width: 320px;
}

.tel-link {
    position: relative;
    text-decoration: none;
    color: $base-color;
    line-height: 1;
    transition: color 0.3s;

    .icon {
        margin-right: 0.2rem;
        color: $active-color;
    }

    &:hover,
    &:focus {
        color: $active-color;
        text-decoration: none;
    }
}

.social-link {
    display: inline-block;
    vertical-align: middle;
    width: 2rem;
    height: 2rem;
    transition: opacity 0.3s;
    background-size: cover;

    & + & {
        margin-left: 1rem;
    }

    &:hover,
    &:focus {
        opacity: 0.7;
    }
}

.facebook {
    background-image: url('../img/facebook.png');
}

.instagram {
    background-image: url('../img/instagram.png');
}

hr {
    margin: 3rem 20%;
    height: 2px;
    opacity: 1;
    background: $secondary-color;

    @media (max-width: 767px) {
        margin: 2rem 5%;
    }
}

.title {
    color: $secondary-color;
}

.sub-title {
    font-size: 1.3rem;
}

.screen {
    padding-top: 3rem;
    padding-bottom: 3rem;

    p {
        margin: 0;
    }

    hr {
        margin-top: 2rem;
        margin-bottom: 2rem;
    }
}



.form-group {
    margin-bottom: 1rem;

    label {
        font-weight: 500;
    }

    &:last-of-type {
        margin-bottom: 0;
    }
}
